:root {
  --color-white: #ffffff;
  --color-dark: #272727;
  --color-gray: #747474;
  --color-dark-translucent: rgba(39, 39, 39, 0.75);
  --color-primary-1: #33d997;
  --color-primary-2: #00ff99;
  --color-secondary: #ff652f;
  --color-tertiary: #6600ff;
}

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Fira Mono";
  font-display: swap;
  src: local("Fira Mono Medium"), local("FiraMono-Medium"), url("../fonts/firamono-medium.woff2") format("woff2"), url("../fonts/firamono-medium.woff") format("woff");
}
@keyframes toggle-open-1 {
  from {
    transform: translateY(0) rotate(0);
  }
  to {
    transform: translateY(10px) rotate(225deg);
  }
}
@keyframes toggle-open-2 {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(315deg);
  }
}
@keyframes toggle-open-3 {
  from {
    transform: translateY(0) rotate(0);
  }
  to {
    transform: translateY(-10px) rotate(315deg);
  }
}
@keyframes menu-open {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
.footer__wrapper, .projects__wrapper, .about__wrapper {
  box-sizing: border-box;
  min-width: 320px;
  max-width: 620px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;
}
@media (min-width: 768px) {
  .footer__wrapper, .projects__wrapper, .about__wrapper {
    max-width: 1137px;
    padding-right: 30px;
    padding-left: 30px;
  }
}

.about__list, .main-menu__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.page {
  scroll-behavior: smooth;
  font: 500 14px "Fira Mono", "Courier New", monospace;
  color: var(--color-white);
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  background-color: var(--color-dark);
}

.logo img {
  display: block;
  width: 173px;
  height: 22px;
}

.title {
  margin-top: 0;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 28px;
  color: var(--color-secondary);
  text-transform: uppercase;
}
.title::before {
  content: "# ";
  color: var(--color-primary-1);
}

.button {
  --color-brand: var(--color-primary-1);
  display: inline-block;
  box-sizing: border-box;
  min-width: 100px;
  max-width: 240px;
  padding: 3px 8px;
  font: inherit;
  vertical-align: middle;
  text-align: center;
  color: var(--color-dark);
  text-transform: lowercase;
  background-color: var(--color-brand);
  border: 2px solid var(--color-brand);
  border-radius: 13px;
  cursor: pointer;
  transition: color 0.3s, background-color 0.3s, opacity 0.3s;
  appearance: none;
}
.button:hover, .button:focus {
  color: var(--color-white);
  background-color: transparent;
}
.button:focus {
  outline: none;
}
.button:active {
  opacity: 0.8;
}
.button--secondary {
  color: var(--color-white);
  background-color: transparent;
}
.button--secondary:hover, .button--secondary:focus {
  color: var(--color-dark);
  background-color: var(--color-brand);
}

.header {
  position: sticky;
  top: 0;
  z-index: 1;
  box-sizing: border-box;
  margin-bottom: 25px;
  background-color: var(--color-dark);
}
.no-js .header {
  position: initial;
}
@media (min-width: 768px) {
  .header {
    min-height: 60px;
    margin-bottom: 35px;
    padding-top: 19px;
    padding-bottom: 19px;
    border-bottom: 1px solid var(--color-gray);
  }
}

.header__wrapper {
  box-sizing: border-box;
  min-width: 320px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1137px;
    padding-right: 30px;
    padding-left: 30px;
  }
}

@media (max-width: 767px) {
  .header__inner {
    display: grid;
    grid-template-columns: minmax(20px, 1fr) minmax(auto, 580px) minmax(20px, 1fr);
    align-items: center;
    box-sizing: border-box;
    min-height: 50px;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: var(--color-dark);
    border-bottom: 1px solid var(--color-gray);
  }
  .header__inner > * {
    grid-column: 2/-2;
  }
}

@media (max-width: 767px) {
  .header__logo {
    grid-row-start: 1;
  }
}

.header__toggle {
  position: relative;
  display: block;
  width: 30px;
  height: 30px;
  padding: 0;
  color: var(--color-white);
  background: none;
  border: none;
  cursor: pointer;
  transition: color 0.3s, opacity 0.3s;
  appearance: none;
}
.header__toggle span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  border-radius: 1px;
}
.header__toggle span:nth-child(1) {
  top: 4px;
}
.header__toggle span:nth-child(2) {
  top: 14px;
}
.header__toggle span:nth-child(3) {
  top: 24px;
}
.header__toggle--opened span:nth-child(1) {
  animation: 0.3s both toggle-open-1;
  animation-direction: inherit;
}
.header__toggle--opened span:nth-child(2) {
  animation: 0.3s both toggle-open-2;
  animation-direction: inherit;
}
.header__toggle--opened span:nth-child(3) {
  animation: 0.3s both toggle-open-3;
  animation-direction: inherit;
}
.no-js .header__toggle {
  display: none;
}
.header__toggle:hover, .header__toggle:focus {
  color: var(--color-secondary);
}
.header__toggle:focus {
  outline: none;
}
.header__toggle:active {
  opacity: 0.6;
}
@media (max-width: 767px) {
  .header__toggle {
    grid-row-start: 1;
    justify-self: end;
  }
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}

.main-menu {
  display: none;
  background-color: var(--color-dark);
}
.main-menu--opened {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: -1;
  display: block;
  animation: 0.3s both menu-open;
}
.no-js .main-menu {
  display: block;
  animation: none;
}
@media (min-width: 768px) {
  .main-menu {
    position: initial;
    z-index: initial;
    display: block;
    max-width: 65%;
    animation: none;
  }
}

@media (min-width: 768px) {
  .main-menu__list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: -5px;
  }
}

.main-menu__item {
  min-height: 50px;
  font-size: 26px;
  color: var(--color-secondary);
  text-transform: uppercase;
  border-bottom: 1px solid var(--color-gray);
}
@media (min-width: 768px) {
  .main-menu__item {
    min-height: initial;
    margin-bottom: 5px;
    padding-left: 20px;
    font-size: 16px;
    border: none;
  }
  .main-menu__item:not(:last-child) {
    margin-right: 10px;
  }
}

.main-menu__link {
  color: inherit;
  overflow-wrap: break-word;
  transition: color 0.3s, background-color 0.3s;
}
.main-menu__link span {
  position: relative;
}
.main-menu__link span::before {
  content: ">";
  position: absolute;
  top: 0;
  left: 0;
  color: transparent;
  transition: color 0.3s;
}
.main-menu__link:hover span::before, .main-menu__link:focus span::before {
  color: var(--color-primary-1);
}
.main-menu__link:focus {
  outline: none;
}
.main-menu__link:active {
  color: var(--color-dark);
  background-color: var(--color-secondary);
}
.main-menu__link:active span::before {
  color: var(--color-dark);
}
@media (max-width: 767px) {
  .main-menu__link {
    display: grid;
    grid-template-columns: minmax(20px, 1fr) minmax(auto, 580px) minmax(20px, 1fr);
    align-items: center;
    padding-top: 10px;
    padding-bottom: 9px;
  }
  .main-menu__link span {
    grid-column: 2/-2;
    padding-left: 30px;
  }
}
@media (min-width: 768px) {
  .main-menu__link span::before {
    left: -20px;
  }
}

.about {
  margin-bottom: 25px;
  padding-bottom: 20px;
  border-bottom: 1px solid var(--color-gray);
}
@media (min-width: 768px) {
  .about {
    margin-bottom: 35px;
    padding-bottom: 35px;
  }
}

@media (min-width: 768px) {
  .about__content {
    display: grid;
    grid-template-columns: 216px 1fr;
    column-gap: 30px;
  }
}

.about__author {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .about__author {
    margin-bottom: 0;
  }
}

.about__photo {
  display: none;
}
.about__photo img {
  width: 216px;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .about__photo {
    display: block;
    margin-bottom: 20px;
  }
}

.about__introduction {
  margin-top: 0;
  margin-bottom: 0;
}

.about__taglist:not(:last-child) {
  margin-bottom: 20px;
}

.about__taglist-name {
  margin-top: 0;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 16px;
  color: var(--color-secondary);
}

.about__list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-bottom: -5px;
}

.about__item {
  margin-bottom: 5px;
}
.about__item:not(:last-child) {
  margin-right: 5px;
}

.tag {
  --color-brand: var(--color-primary-1);
  display: flex;
  max-width: 280px;
  text-transform: lowercase;
  overflow-wrap: break-word;
  background-color: var(--color-brand);
  border: 2px solid var(--color-brand);
  border-radius: 13px;
}

.tag__name {
  box-sizing: border-box;
  max-width: 100%;
  padding: 3px 8px;
  color: var(--color-dark);
}
.tag--extended .tag__name {
  max-width: 50%;
  padding-right: 5px;
}

.tag__addintion {
  box-sizing: border-box;
  max-width: 100%;
  padding: 3px 8px 3px 5px;
  background-color: var(--color-dark);
  border-top-right-radius: 13px;
  border-bottom-right-radius: 13px;
}

.projects {
  margin-bottom: 25px;
  padding-bottom: 20px;
  border-bottom: 1px solid var(--color-gray);
}
@media (min-width: 768px) {
  .projects {
    margin-bottom: 35px;
    padding-bottom: 35px;
  }
}

.projects__description {
  margin-top: 0;
  margin-bottom: 20px;
}

.projects__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, 280px);
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .projects__list {
    grid-template-columns: repeat(auto-fill, 339px);
    gap: 30px;
    margin-bottom: 30px;
  }
}

.projects__links {
  display: flex;
  justify-content: center;
}

.work {
  position: relative;
  width: 280px;
  min-height: 280px;
  overflow: hidden;
  background-color: var(--color-gray);
  border-radius: 9px;
}
.work:hover .work__content, .work:focus-within .work__content {
  opacity: 1;
}
@media (min-width: 768px) {
  .work {
    width: 339px;
    min-height: 255px;
  }
}

.work__image img {
  display: block;
  width: 280px;
}
@media (min-width: 768px) {
  .work__image img {
    width: 339px;
  }
}

.work__content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 58px 20px 20px;
  background-color: var(--color-dark-translucent);
  opacity: 0;
  transition: opacity 0.3s;
}
.work__content::before, .work__content::after {
  position: absolute;
  top: 20px;
  display: block;
  padding: 2px 5px;
  font-size: 12px;
  background-color: var(--color-gray);
  border-radius: 5px;
}
.work--training .work__content::before {
  content: "training project";
  left: 20px;
}
.work--fixed .work__content::after {
  content: "fixed";
  right: 20px;
}
.work--adaptive .work__content::after {
  content: "adaptive";
  right: 20px;
  background-color: var(--color-secondary);
}
.work--responsive .work__content::after {
  content: "responsive";
  right: 20px;
  background-color: var(--color-tertiary);
}

.work__info {
  margin-bottom: 20px;
}

.work__title {
  margin-top: 0;
  margin-bottom: 10px;
  padding-bottom: 10px;
  font-weight: 500;
  font-size: 18px;
  text-align: center;
  border-bottom: 1px solid var(--color-primary-2);
}

.work__description {
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
}

.work__links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: -5px;
  padding-top: 20px;
  border-top: 1px solid var(--color-primary-2);
}

.work__link {
  margin-right: 2.5px;
  margin-bottom: 5px;
  margin-left: 2.5px;
  color: var(--color-white);
}
.work__link:hover, .work__link:focus {
  color: var(--color-white);
}

.footer {
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .footer {
    margin-bottom: 35px;
  }
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__logo {
  margin-bottom: 20px;
}

.footer__contacts {
  margin-bottom: 20px;
}

.footer__copyright {
  font-size: 12px;
  text-align: center;
}

.contacts__text {
  margin-top: 0;
  margin-bottom: 20px;
  text-align: center;
}

.contacts__links {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: -5px;
  padding-bottom: 20px;
}
.contacts__links::before {
  content: "";
  position: absolute;
  bottom: 5px;
  left: 50%;
  width: 60px;
  height: 2px;
  background-color: var(--color-primary-1);
  border-radius: 1px;
  transform: translateX(-50%);
}

.contacts__link {
  margin-right: 2.5px;
  margin-bottom: 5px;
  margin-left: 2.5px;
}